  background-color: transparent;
  color: rgb(0, 0, 0);
  font: field;
  text-rendering: optimizeLegibility;
  line-height: normal;
  text-align: start;
  text-transform: none;
  word-spacing: normal;
  letter-spacing: normal;
  cursor: text;
  text-indent: 0;
  user-select: text;
  text-shadow: none;
  overflow-clip-box: padding-box content-box;
