  white-space: nowrap !important;
  word-wrap: normal !important;
  text-align: start;
  cursor: default;
  box-sizing: border-box;
  user-select: none;
  appearance: menulist;
  border-width: 2px;
  border-style: inset;
  text-indent: 0;
  overflow: hidden-unscrollable;
  text-shadow: none;
